    text-align: right;  Center text 
    position: absolute;  Position text 
    top: 88%;  Position text in the middle 
    left: 70%;  Position text in the middle 
    transform: translate(-46%, -45%);  Position text in the middle 
    mix-blend-mode: screen;  This makes the cutout text possible 
    overflow: hidden;
  }

  */
  .bg-w {
    position: absolute;
    z-index: -1;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
  }

  /* global */
  /* html {line-height: 1.5;} */
  .br-nr {background-repeat: no-repeat;}

  .b-s-l {border: solid 10%;}
  .b-s-m {border: solid 4%;}
  .b-s-s {border: solid 1%;}
  .b-n {border: none;}

  .bg-c-fw {background-color: rgba(255, 255, 255,.7);}
  .bg-c-fg {background-color: rgba(170, 170, 170, .9);}
  .bg-c-n {background-color: transparent;}
  .bg-c-w {background-color: rgb(255, 255, 255);}
  .bg-c-fk {background-color: rgba(0, 0, 0, .5);}

  .g-fk {
    background: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(0, 0, 0, .8)),
      color-stop(200%, rgba(0, 0, 0, 0.2))
    );
    background: linear-gradient(
      rgba(0, 0, 0, .8),
      rgba(0, 0, 0, 0.2) 200%
    );
  }

  .c-dg {color: rgb(51, 51, 51);}
  .c-k {color: rgb(0, 0, 0);}
  .c-g {color: rgb(170, 170, 170);}
  .c-w {color: rgb(255, 255, 255);}
  .c-fw{color: rgba(255, 255, 255,.7);}
  .c-n {color: transparent;}


  .f-f {font-family: "Inknut Antiqua", serif;}

  .fz-jjj {font-size: 500%;}  
  .fz-jj {font-size: 300%;}  
  .fz-j {font-size: 200%;}
  .fz-xl {font-size: 150%;}
  .fz-l {font-size: 100%;}
  .fz-m {font-size: 70%;}
  .fz-s {font-size: 50%;}


  .o-h {opacity: 0.75;}
  .o-m {opacity: 0.5;}
  .o-l {opacity: 0.25;}

  

  /* shadow */
  .s {
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 5px 5px 14px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
  }

  .td-n {text-decoration: none;}
  .ts-i {font-style: italic;}


  /* ------------------ON FOCUS ------------------ */
  .bg-c-fw-fc:focus {background-color: rgba(255, 255, 255,.6);}
  .bg-c-w-fc:focus {background-color: rgb(255, 255, 255);}
  .c-w-fc:focus {color: rgb(255, 255, 255);}
  .c-dg-fc:focus {color: rgb(51, 51, 51);}


  /* grow, shrink */
  .t-g-fc:focus {
    transform: scale(1.05);
    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
  }
  .t-s-fc:focus {
    transform: scale(0.95);
    -webkit-transform: scale(0.95);
    -moz-transform: scale(0.95);
  }

  /* add shadow */
  .s-fc:focus {
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 5px 5px 14px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
  }

  /* ------------------ ON HOVER ------------------ */
  
  .bg-c-fw-hv:hover {background-color: rgb(255, 255, 255, .6);}
  .c-w-hv:hover {color: rgb(255, 255, 255);}
  .c-dg-hv:hover {color: rgb(51, 51, 51);}

  .t-g-hv:hover {
    transform: scale(1.05);
    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
  }
  .t-s-hv:hover {
    transform: scale(0.95);
    -webkit-transform: scale(0.95);
    -moz-transform: scale(0.95);
  }

  /* add shadow */
  .s-hv:hover {
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 5px 5px 14px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
  }

}

